.header {
  border: 1px solid #282727;
  justify-content: space-between;
  display: flex;
}

.logo {
  width: 100px;
  margin: 10px;
  padding: 10px;
}

.nav-items {
  padding-right: 20px !important;
}

.nav-items > ul {
  font-size: 21px;
  display: flex;
}

.nav-items > ul > li {
  margin: 10px;
  padding: 10px;
  list-style: none;
}

.search {
  padding: 15px;
}

.res-container {
  flex-wrap: wrap;
  margin: 20px;
  display: flex;
}

.res-card {
  border: 1px solid #282727;
  width: 220px;
  margin: 10px;
  padding: 10px;
}

.res-card:hover {
  cursor: pointer;
  border: 1.5px solid #282727;
  box-shadow: 0 0 10px 5px #0000001a;
}

.res-logo {
  width: 100%;
  height: 50%;
}
